.display2{
  color: $primary;
}

.doctors-slider{
  margin-top: 55px;
  .swiper{
    &-container{
      width: calc(100% + 30px);
    }
    &-slide{
      padding-right: 30px;
    }
    &-nav{
      margin-top: 40px;
      display: flex;
      justify-content: center;
    }
    &-button{
      position: static;
      margin: 0 5px;
      img{
        width: 16px;
      }
    }
  }
}

.swiper-container{
  overflow: hidden;
}

.partners .swiper-container{
  width: calc(100% - 374px);
}

.index-news{
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 40px;
  }
  &__link{
    margin: 0;
    padding: 0 24px;
    font-size: 20px;
    height: 50px;
    border-radius: 10px;
    &:hover{
      color: #fff;
      path{
        fill: #fff;
      }
    }
    span{
      text-decoration: none;
    }
    svg{
      width: 8px;
      margin-left: 11px;
    }
  }
}


@media screen and (max-width: 1200px){
  .partners .swiper-container{
    width: calc(100% - 270px);
  }
}


@media screen and (max-width: 992px){
  .doctors-slider{
    margin-top: 42px;
    .swiper-nav{
      margin-top: 30px;
    }
  }

  .partners .swiper-container{
    width: calc(100% - 144px);
  }
}


@media screen and (max-width: 768px){
  .doctors-slider{
    .swiper-container{
      width: calc(100% + 20px);
    }
    .swiper-slide{
      padding-right: 20px;
    }
  }
}


@media screen and (max-width: 576px){
  .doctors-slider{
    margin-top: 10px;
    .swiper-slide{
      padding-top: 32px;
    }
  }
}
