h1{
  font-family: 'Rubik';
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  line-height: 40px;
  display: flex;
  align-items: flex-end;
  color: #009BE3;
}

.analiz{
  @media screen and (min-width: 577px){
    &__top{
      br{
        display: none;
      }
    }
  }
  .service-item{
    justify-content: center;
    height: 76px;
    svg{
      width: 34px;
    }
  }
  .article-text{
    margin-top: 80px;
  }
  .analiz__search{
    margin: 65px 0 0 auto;
  }
  .services2__items,
  .analiz__items{
    margin-top: 50px;
  }
  .service-item{
    padding: 17px 20px;
    border-radius: 10px;
    border: 1px solid $primary;
    display: flex;
    align-items: center;
    color: $primary;
    &:hover{
      background-color: $primary;
      color: #fff;
      path{

        stroke: #fff;
      }
    }
    path{
      fill: transparent;
      transition: all .4s;
    }
    svg{
      width: 43px;
      height: auto;
      flex-shrink: 0;
      margin-right: 20px;
    }
    &__text{
      font-size: 22px;
      line-height: 118%;
      font-weight: 700;
    }
  }
}

.grid2{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 15px));
  gap: 30px;
}

.analiz-item{
  display: grid;
  grid-template-columns: 50% 50%;
  margin-top: 80px;
  &__col{
    padding: 20px;
    background-color: $primary;
    border-radius: 10px 0 0 10px;
    color: #fff;
  }
  &__col2{
    padding: 20px;
    border: 1px solid $primary;
    border-radius: 0 10px 10px 0;
  }

}

.analiz-method{
  &__top{
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    font-size: 18px;
    line-height: 160%;
    font-weight: 500;
  }
  &__items{
    margin-top: 15px;
    display: grid;
    grid-template-columns: calc(100% - 182px) 166px;
    gap: 16px;
  }
  &__item{
    display: flex;
    align-items: center;
    &-icon{
      width: 30px;
      height: 30px;
      margin-right: 10px;
      img{
        max-width: 100%;
        max-height: 100%;
        display: block;
      }
    }
    &-title{
      font-size: 14px;
      line-height: 150%;
      font-weight: 500;
    }
  }
}

.analiz-info{
  &__items{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__col,
  &__col2{
    @include grid100gap(15px);
  }
  &__col2{
    gap: 12px;
    text-align: right;
  }
  &__item{
    font-size: 14px;
    line-height: 160%;
    font-family: 'Montserrat',sans-serif;
    span{
      font-size: 20px;
      font-weight: 600;
      color: $primary;
    }
  }
  &__bottom{
    padding-top: 24px;
    margin-top: 20px;
    border-top: 1px solid rgba($primary, 0.5);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__sum{
    font-size: 18px;
    span{
      font-size: 22px;
      font-weight: 600;
      color: $primary;
    }
  }
  &__btn{
    padding: 15px 29px;
    border-radius: 10px;
  }
}

.article-text{
  h2{
    font-size: 34px;
    line-height: 125%;
    color: $primary;
    font-weight: 700;
    margin-bottom: 50px;
  }
}

.analiz__item{
  .info-card__btn{
    border-radius: 10px 10px 0 0;
  }
}


@media screen and (max-width: 992px){
  .grid-tablet{
    grid-template-columns: 100%;
    gap: 20px;
  }

  .analiz{
    .service-item{
      &__text{
        font-size: 19px;
        span{
          display: none;
        }
      }
      svg{
        display: block;
        width: 34px;
        margin-right: 20px;
      }
    }
    .services2__items,
    .analiz__search,
    .analiz__items,
    .article-text{
      margin-top: 32px;
    }
    .article-text{
      p{
        font-size: 16px;
      }
      ul {
        li{
          font-size: 16px;
          padding-left: 15px;
          &:before{
            width: 3px;
            height: 3px;
            top: 10px;
          }
        }
      }
    }
  }

  .analiz-item{
    margin-top: 32px;
    grid-template-columns: 100%;
    &__col{
      border-radius: 10px 10px 0 0;
    }
    &__col2{
      border-radius: 0 0 10px 10px;
    }
  }

  .article-text{
    h2{
      font-size: 22px;
      margin-bottom: 32px;
    }
  }

  .analiz__item{
    .info-card__title{
      font-weight: 400;
    }
  }
}


@media screen and (max-width: 576px){
  .analiz {
    .service-item {
      padding: 0 22px;
      justify-content: unset;
    }
  }

  .analiz-method{
    &__top{
      font-size: 16px;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 12px;
      margin-top: 20px;
    }
    &__item{
      &-icon{
        width: 26px;
        height: 26px;
        margin-right: 15px;
      }
      &-title{
        font-size: 13px;
        font-weight: 400;
      }
    }
  }

  .analiz-info{
    &__items{
      display: block;
    }
    &__col2{
      gap: 15px;
      text-align: left;
      margin-top: 15px;
    }
    &__item{
      font-family: Rubik,sans-serif;
      font-size: 13px;
      span{
        font-size: 18px;
      }
    }
    &__bottom{
      padding-top: 20px;
      display: block;
    }
    &__sum{
      font-size: 16px;
      span{
        font-size: 20px;
      }
    }
    &__btn{
      margin: 10px 0 0;
      width: 100%;
    }
  }
}
