.footer{
  padding: 50px 0;
  background: $primary;
  color: #fff;
  &__inner{
    align-items: unset;
  }
}

.footer-nav{
  &__title{
    font-size: 18px;
    line-height: 117%;
    font-weight: 600;
  }
  ul{
    margin-top: 25px;
    @include grid100gap(10px);
    a{
      font-size: 16px;
      line-height: 119%;
      &:hover{
        text-decoration: underline !important;
      }
    }
  }
}

.footer-info{
  .footer__phone{
    text-align: right;
    &-link{
      display: inline-block;
      &:hover{
        color: #fff;
        text-decoration: underline !important;
      }
    }
  }
  &__bottom{
    margin-top: 30px;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
  }
  &__conf,
  &__c{
    font-size: 15px;
    line-height: 120%;
  }
  &__conf{
    text-decoration: underline;
  }
  &__c{
    margin-top: 24px;
  }
}

.footer-socials{
  padding: 30px 0;
  display: flex;
  justify-content: flex-end;
  margin-top: 30px;
  border-top: 1px solid rgba(#fff,.3);
  border-bottom: 1px solid rgba(#fff,.3);
  &__item{
    width: 48px;
    margin-right: 28px;
    &:last-child{
      margin-right: 0;
    }
    svg{
      width: 100%;
    }
    &:hover{
      opacity: .9;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 32px 0 90px;
    text-align: left;
    &__inner{
      @include grid100gap(32px);
    }
  }

  .footer-info {
    .footer__phone {
      text-align: left;
    }
    &__bottom{
      align-items: flex-start;
    }
  }

  .footer-socials{
    justify-content: unset;
  }
}


