.site-map{
  &__groups{
    margin-top: 80px;
    @include grid100gap(50px);
  }
}

.map-group{
  &__title{
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
    padding-bottom: 35px;
    margin-bottom: 50px;
    border-bottom: 1px solid #D3D3D3;
  }
  &__links{
    column-count: 4;
    column-gap: 25px;
    @media screen and (min-width: 993px){
      &._grid{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
      }
    }
    li{
      display: inline-block;
      width: 100%;
      margin-bottom: 10px;
      &:last-child{
        margin-bottom: 11px;
      }
    }
    a{
      font-size: 15px;
      line-height: 160%;
      &:hover{
        text-decoration: underline !important;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .site-map{
    &__groups{
      margin-top: 32px;
      gap: 32px;
    }
  }

  .map-group{
    &__title{
      padding-bottom: 30px;
      margin-bottom: 30px;
      font-size: 18px;
    }
    &__links{
      column-count: 3;
      @media screen and (min-width: 769px){
        &._grid{
          display: grid;
          grid-template-columns: 1fr 1fr 1fr;
        }
      }
    }
  }
}


@media screen and (max-width: 768px){
  .map-group{
    &__links{
      column-count: 2;
    }
  }
}
